<template>
    <a-card style="margin-top:20px;margin-bottom: 20px">
        <div class="ant-upload-list ant-upload-list-text" v-for="(row,index) in list_computed">
            <div class="ant-upload-list-item ant-upload-list-item-done">
                <div class="ant-upload-list-item-info">
                    <span>
                        <i class="anticon anticon-paper-clip"></i>
                        <a :href="row.url"
                           target="_blank"
                           rel="noopener noreferrer"
                           :download="row.name"
                           :title="row.name"
                           class="ant-upload-list-item-name">
                            {{row.name}}
                        </a>
                    </span>
                </div>
            </div>
        </div>
    </a-card>
</template>

<script>
    export default {
        props: {
            list: {
                required: true,
                type: Array,
            }
        },
        data() {
            return {
                baseUrl: this.$store.state.common.fileBaseUrl,
            }
        },
        computed: {
            list_computed() {
                let temp = this.list;
                for (let i = 0; i < temp.length; i++) {
                    temp[i].url = this.baseUrl + temp[i].url
                }
                return temp
            }
        },
    }
</script>

