<template>
    <div class="columns">
        <div class="xwsh_nr">
            <div style="padding: 30px 5px 30px 5px">
                <h1>{{comment.comment_title}}</h1>
                <h2>{{comment.leave_at_format}}</h2>
                <a-divider/>
                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <div v-html="comment.comment_content"></div>
                    </a-col>
                </a-row>

                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <downloadFiles v-if="files.length" :list="files"/>
                    </a-col>
                </a-row>

            </div>
        </div>

        <slot name="suffix"></slot>
    </div>
</template>

<script>
    import downloadFiles from '@/components/download/Files'

    export default {
        components: { downloadFiles},
        props: {
            comment: {type: Object}
        },
        computed: {
            files() {
                if(!this.comment.hasOwnProperty('more')){
                    return [];
                }
                if (!this.comment.more.hasOwnProperty('files')){
                    return [];
                }
                return Array.isArray(this.comment.more.files) ? this.comment.more.files : [];
            },
        },
        data() {
            return {}
        },
        mounted() {
        }
    }
</script>
