<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <CheckComment :comment="comment"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../留言列表我发起的/components/TopButtons'
    import CheckComment from '@/components/comment/check'
    import {getComment} from '@/api/comment'

    export default {
        components: {
            TopButtons, CheckComment
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        data() {
            return {
                comment: {}
            }
        },
        methods: {
            fetch() {
                getComment(this.thisId)
                    .then(res => {
                        this.comment = res.data;
                    })
            }
        },
        created() {
            this.fetch();
        }
    }
</script>
